import { Component, OnInit } from '@angular/core';
import {ActionSheetController, NavController} from 'ionic-angular';
import {MapPage} from "../map/map";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit {
  public saludos = ["hola", "holi", "que pedo", "que pex", "que onda"];
  public messages = [];
  public actionSheet;
  public message: String;

  public messagesTest = ["muestrame la lista", "recomiendame lugares de entretenimiento",
    "recomiendame lugares de comida", "recomiendame lugares de cultura"];

  constructor(public navCtrl: NavController, public actionSheetCtrl: ActionSheetController) {
    this.actionSheet = this.actionSheetCtrl.create({
      title: 'Seleccione una opción',
      buttons: [
        {
          text: 'Entretenimiento',
          icon: 'beer',
          handler: () => {
            this.optionHandler('entretenimiento');
          }
        },
        {
          text: 'Cultura',
          icon: 'brush',
          handler: () => {
            this.optionHandler('cultura');
          }
        },
        {
          text: 'Comida',
          icon: 'pizza',
          handler: () => {
            this.optionHandler('comida');
          }
        }
      ]
    });
  }

  onClickMessage(message) {
    this.messages.push({
      text: message,
      who: 'mine'
    });

    switch (message) {
      case this.messagesTest[0]:
        this.actionSheet = this.actionSheetCtrl.create({
          title: 'Seleccione una opción',
          buttons: [
            {
              text: 'Entretenimiento',
              icon: 'beer',
              handler: () => {
                this.optionHandler('entretenimiento');
              }
            },
            {
              text: 'Cultura',
              icon: 'brush',
              handler: () => {
                this.optionHandler('cultura');
              }
            },
            {
              text: 'Comida',
              icon: 'pizza',
              handler: () => {
                this.optionHandler('comida');
              }
            }
          ]
        });

        this.messages.push({
          text: 'Ahi te van :3',
          who: 'other'
        });

        this.actionSheet.present();
        break;
      default:
        message = this.matchMessage(message);

        window.setTimeout(() => {
          this.messages.push({
            text: (`Excelente, enseguida te muestro los mejores lugares de ${message}`),
            who: 'other'
          });
        }, 1000);

        window.setTimeout(() => {
          this.navCtrl.push(MapPage, {
            "categoria": message
          });
        }, 3500);
    }
  }

  matchMessage(message) {
    var msg = message.split(" ");

    console.log(msg[3]);

    return msg[3];
  }

  checkMatch(where, text){
    if(where == 'saludos'){
      for(var i = 0; i < this.saludos.length; i++){
        if(text == this.saludos[i]){
          return true;
        }
      }
      return false;
    }
  }

  onSubmit(){
    this.optionHandler(this.message);
    this.message = "";
  }

  optionHandler(text: String){
    let message = {
      text: text,
      who: 'mine'
    };

    this.messages.push(message);

    if(message.who == 'mine') {
      if(text == 'entretenimiento' || text == 'cultura' || text == 'comida'){
        window.setTimeout(() => {
          this.messages.push({
            text: (`Excelente, enseguida te muestro los mejores lugares de ${text}`),
            who: 'other'
          });
        }, 1000);

        window.setTimeout(() => {
          this.navCtrl.push(MapPage, {
            "categoria": text
          });
        }, 3500);
      } else {
        if (this.checkMatch('saludos', text)) {
          this.messages.push({
            text: ("Hola, selecciona una opción para ayudarte"),
            who: 'other'
          });

          this.actionSheet = this.actionSheetCtrl.create({
            title: 'Seleccione una opción',
            buttons: [
              {
                text: 'Entretenimiento',
                icon: 'beer',
                handler: () => {
                  this.optionHandler('entretenimiento');
                }
              },
              {
                text: 'Cultura',
                icon: 'brush',
                handler: () => {
                  this.optionHandler('cultura');
                }
              },
              {
                text: 'Comida',
                icon: 'pizza',
                handler: () => {
                  this.optionHandler('comida');
                }
              }
            ]
          });

          this.actionSheet.present();

        } else {
          this.messages.push({
            text: 'Sorry, no he podido detectar que buscas :c',
            who: 'other'
          });
        }
      }
    }
  }

  orderMessages(){
    this.messages.reverse();
  }

  ngOnInit(){
    window.setTimeout(() => {
      this.messages.push({
        text: 'Bienvenido a SafeWey',
        who: 'other'
      });
      this.orderMessages();

    }, 500);

    window.setTimeout(() => {
      this.messages.push({
        text: 'Por favor selecciona una opción para ayudarte',
        who: 'other'
      });
    }, 1500);

    window.setTimeout(() => {
      this.actionSheet.present();
    }, 2.5 * 1000);
  }
}



