import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the MapPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

declare var google;

@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

  city = {
    latitude: 0,
    longitude: 0
  };
  category: String;
  map: any;
  lugares = [];

  coordenadas = [
    {
      id: 0,
      latitude: 21.009145,
      longitude: -101.273570
    },
    {
     id: 1,
     latitude: 21.012183,
     longitude: -101.274924
    },
    {
      id: 2,
      latitude: 21.013195,
      longitude: -101.273747
    },
    {
      id: 3,
      latitude: 21.015799,
      longitude: -101.274648
    },
    {
      id: 4,
      latitude: 21.009045,
      longitude: -101.274281
    },
    {
      id: 5,
      latitude: 21.004702,
      longitude: -101.2847657
    },
    {
      id: 6,
      latitude: 21.001918,
      longitude:  -101.283425
    },
    {
      id: 7,
      latitude: 21.008618,
      longitude: -101.282223
    },
    {
      id: 8,
      latitude: 21.001918,
      longitude: -101.283425
    },
    {
      id: 9,
      latitude: 21.009350,
      longitude: -101.281934
    },
    {
      id: 10,
      latitude: 21.011213,
      longitude: -101.280432
    }
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams) {

    this.category = this.navParams.get("categoria");
    this.switchCategory(this.category);
  }

  ionViewDidLoad() {
    this.loadMap();
  }

  switchCategory(name){
    switch (name) {
      case 'entretenimiento':
        this.city = {
          latitude: 21.009145,
          longitude: -101.273570
        };

        this.lugares.push({
          id: 0,
          title: 'LA VENTINA Café - Bar - Resto',
          likes: 5,
          dislikes: 3
        });

        this.lugares.push({
          id: 1,
          title: 'Centro de Convenciones de Guanajuato',
          likes: 10,
          dislikes: 1
        });

        this.lugares.push({
          id: 2,
          title: 'Auditorio del estado de Guanajuato',
          likes: 7,
          dislikes: 2
        });

        this.lugares.push({
          id: 3,
          title: 'Internet y Abarrotes',
          likes: 6,
          dislikes: 3
        });

        break;
      case 'cultura':
        this.city = {
          latitude: 21.009045,
          longitude: -101.274281
        };

        this.lugares.push({
          id: 4,
          title: 'Museo Ex Hacienda San Gabriel de Barrera',
          likes: 10,
          dislikes: 3
        });

        this.lugares.push({
          id: 5,
          title: 'Kiosko de Marfil',
          likes: 13,
          dislikes: 1
        });

        this.lugares.push({
          id: 6,
          title: 'Casa Museo Gene Byron',
          likes: 13,
          dislikes: 1
        });

        break;
      case 'comida':
        this.city = {
          latitude: 21.008618,
          longitude: -101.282223
        };

        this.lugares.push({
          id: 7,
          title: 'Restaurante de los santos',
          likes: 10,
          dislikes: 3
        });

        this.lugares.push({
          id: 8,
          title: 'La Diabla',
          likes: 13,
          dislikes: 3
        });

        this.lugares.push({
          id: 9,
          title: 'Sabor y Arte',
          likes: 16,
          dislikes: 3
        });

        this.lugares.push({
          id: 10,
          title: 'Portofino Ristorante',
          likes: 11,
          dislikes: 2
        });

        break;
    }
  }

  clickPlace(id) {
    this.city = this.coordenadas[id];

    this.loadMap();
  }

  loadMap() {
      let latitude = 21.003765;
      let longitude = -101.270291;
      console.log(latitude, longitude);

      // create a new map by passing HTMLElement
      let mapEle: HTMLElement = document.getElementById('map_canvas');

      // create LatLng object
      let myLatLng = {lat: latitude, lng: longitude};

      // create map
      this.map = new google.maps.Map(mapEle, {
        center: myLatLng,
        zoom: 17
      });

      google.maps.event.addListenerOnce(this.map, 'idle', () => {
      new google.maps.Marker({
        position: myLatLng,
        map: this.map,
        title: 'Hello World!'
      });
      mapEle.classList.add('show-map');

      new google.maps.Circle({
        strokeColor: '#00ff00',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: '#00ff00',
        fillOpacity: 0.35,
        map: this.map,
        center: {lat: this.city.latitude, lng: this.city.longitude},
        radius: 50
      });

      var directionsService = new google.maps.DirectionsService();
      var directionsDisplay = new google.maps.DirectionsRenderer();

      directionsDisplay.setMap(this.map);

      var myLocation = new google.maps.LatLng(latitude, longitude);
      var place = new google.maps.LatLng(this.city.latitude, this.city.longitude);

      var request = {
        origin: myLocation,
        destination: place,
        travelMode: google.maps.TravelMode.WALKING
      };

      directionsService.route(request, function(response, status) {
        if(status == google.maps.DirectionsStatus.OK) {
          directionsDisplay.setDirections(response)
        }
      })
      }
  );
}

}



